// @ts-ignore
import ReactDOM from 'react-dom';
// @ts-ignore
import { browserConfig } from '@ctsj/router';
// @ts-ignore
import { createComponent } from './component';
import * as Actions from './emit/actions';
import * as Emitter from './emit/emitter';
import ReactApp from './plugins/reactApp';
import VueApp from './plugins/vueApp';
import * as Types from './types';
import Flow from './flow';
// 路由的配置
var router = [];
/**
 * loop - 循环构建路由配置
 * @param config
 * @param routes
 * @param el
 */
function loop(config, routes, el) {
    for (var i = 0; i < config.length; i++) {
        var item = config[i];
        if ('redirect' in item) {
            routes.push({
                path: item.path,
                redirect: item.redirect,
            });
        }
        else {
            var children = [];
            loop(item.routes || [], children, el);
            routes.push({
                path: item.path,
                component: createComponent(item, el),
                routes: children,
            });
        }
    }
}
/**
 * register - 路由的配置
 * @param config - 路由的配置
 * @param el - 渲染的el
 * @return Promise
 */
function register(config, el) {
    // 需要对ctsj-router的路由配置进行定制
    return new Promise(function (resolve) {
        loop(config, router, el);
        resolve();
    });
}
/**
 * start - 开启
 * @param el
 */
function start(el) {
    return new Promise(function (resolve) {
        // @ts-ignore
        ReactDOM.render(browserConfig(router), el, function () {
            // 订阅客户端数据流更改事件
            Flow.subscribeFlowChange();
            resolve();
        });
    });
}
export default { register: register, start: start, Actions: Actions, ReactApp: ReactApp, VueApp: VueApp, Types: Types, Emitter: Emitter, Flow: Flow };
